@import "src/Constant/colors";
.DepositModal {
	.modal-dialog {
		.modal-content {
			border-radius: 0.5rem;
			background-color: $secondary_light_black;
			color: white;
			padding: 0.5rem;
		}
		.ModalCloseBtn {
			position: absolute;
			right: 1.8rem;
			top: 1.8rem;
		}
	}
	.ModalTitle {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
		.modal-title {
			font-size: 1.125rem;
		}
	}
	.ModalFooter {
		.ModalBtn {
			width: 100%;
			background-color: $secondary_light_black;
			border-radius: 0.75rem;
			border: 1px solid $primary_blue;
			text-align: left;
			font-size: 1.125rem;
			font-weight: bold;
			color: white;
			padding: 1rem;
			&:active {
				border-color: none;
			}
			&:focus {
				box-shadow: unset;
			}
			&:hover {
				border: 1px solid $primary_light_blue;
			}
		}
	}
}
