@import "src/Constant/colors";
.Deposit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $primary_white;
  font-size: 1.25rem;
  font-weight: 600;
}

.Card {
  border-width: 1px;
  width: 560px;
}

.InnerCard {
  // background-color: #1f1665;
  // border: 3px solid #34bbf1;
  border-radius: 8px;
  padding: 10px;
  padding-top: 18px;
  width: 560px;
  position: relative;
  .BlueLeft {
    position: absolute;
    left: 30px;
    top: 80px;
    transform: rotate(10deg) scale(0.8, 0.8);
  }
  .BlueRight {
    position: absolute;
    left: 460px;
    top: 80px;
    transform: rotate(-0deg) scale(-1.2, 1.2);
  }
  .YellowLeft {
    position: absolute;
    left: 0px;
    top: 0px;
    transform: rotate(-10deg) scale(0.8, 0.8);
  }
  .YellowRight {
    position: absolute;
    left: 460px;
    top: 0px;
    transform: rotate(10deg) scale(-0.5, 0.5);
  }
  .SIconFirst {
    color: $custom_light_green;
    opacity: 0.5;
    position: absolute;
    left: 8px;
    top: 193px;
    transform: rotate(-15deg);
  }
  .SIconSecond {
    color: $custom_purple;
    position: absolute;
    left: 585px;
    top: 97px;
    transform: rotate(-10deg);
  }
  .SIconThird {
    color: $custom_light_green;
    opacity: 0.6;
    position: absolute;
    right: 48px;
    top: 10px;
    transform: rotate(45deg);
  }
  .CIconFirst {
    color: $custom_light_green;
    opacity: 0.4;
    position: absolute;
    left: 95px;
    top: 1px;
  }
  .CIconSecond {
    color: $custom_purple;
    position: absolute;
    left: 0px;
    top: 16px;
  }
  .CIconThird {
    color: $custom_light_green;
    opacity: 0.6;
    position: absolute;
    right: 6px;
    top: 193px;
  }
}

.Balance {
  font-size: 75px;
  line-height: 6rem;
  font-weight: bold;
  background-image: linear-gradient(77.6deg, $custom_light_green 0%, #ff77e1 48.47%, #ffed47 88.67%);
  color: transparent;
  -webkit-background-clip: text;
}

// .WeeklyPrize {
//   color: #9183d5;
//   font-size: 19px;
// }
.PrizeEveryDay {
  .PrizeLink {
    color: $custom_light_green;
    background-color: transparent;
    border-width: 0;
    text-decoration: underline;
    padding: 0;
    &:hover {
      opacity: 0.5;
    }
  }
}

.DrawClose {
  color: $custom_light_purple;
  font-size: 19px;
  padding-top: 0.5rem;
  padding-bottom: 0.25rem;
}

.DailyPrize {
  color: $custom_light_green;
  border: 1px solid $custom_light_green;
  background-color: transparent;
  font-family: Titillium Web, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji,
    Segoe UI Symbol, Noto Color Emoji;
  border-width: 2px;
  border-radius: 0.5rem;
  padding: 0.25rem 0.5rem;
  font-size: 1rem;
  font-weight: 600;
}

.DepositUSDC {
  font-size: 1.25rem;
  padding-bottom: 0.65rem;
  justify-content: center;
  .dropdown {
    .dropdown-toggle {
      background-color: rgba(#6c757d, 0.3);
    }
    .dropdown-menu {
      background-color: rgba(#6c757d, 0.1);
      .dropdown-item {
        display: flex;
        align-items: center;
      }
    }
  }
  .DropBtnContainer {
    border-radius: 0.5rem;
    background-color: rgba(255, 255, 255, 0.15);
    border: none;
    color: white;
    .DropBtnText {
      font-size: 1.25rem;
      font-weight: bold;
    }
    &:hover {
      // border-color: #35f0d0;
      border: 1px solid $primary_light_blue;
    }
    &:active {
      border-width: 1;
      background-color: rgba(255, 255, 255, 0.15) !important;
    }
    &:focus {
      box-shadow: unset;
    }
  }

  // .btn-light:hover{
  //   color: white !important;
  // }
}

