.notchedOutline {
  border-width: 0;
}

.Amount {
  display: flex;
  align-items: flex-start;
  font-size: 1rem;
  opacity: 0.8;
}

.USDCInput {
  left: 25px;
  top: 19px;
  font-size: 1.5rem;
  font-weight: bold;
}

.InputValue {
  width: 100%;
  padding: 1rem;
  padding-right: 2rem;
  background-color: $secondary_light_black;
  border-radius: 0.75rem;
  border-style: solid;
  border-color: $secondary_light_black;
  text-align: right;
  font-size: 1.6rem;
  font-weight: bold;
  color: white;
  &:hover {
    border-color: $primary_light_blue;
  }
  &:focus-visible {
    outline: none;
    border: 1px solid $primary_light_blue;
  }
  // &:focus-visible{
  //   border-color: linear-gradient(77.6deg, #17e1fd 0%, #ff77e1 48.47%, #ffed47 88.67%);
  // }
}

.Issue {
  display: flex;
  justify-content: space-between;
  margin-top: 0.5rem;
  width: 100%;
  background-color: $secondary_light_black;
  border-radius: 0.5rem;
  border-width: 0;
  padding: 0.5rem 1rem;
  color: white;
}
.NetworkFee {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 0.5rem;
  border-width: 0;
  font-size: 1rem;
  color: white;
  .Estimate {
    color: #cdc5df;
  }
}
.ConnectButton {
  width: 100%;
  padding: 10px;
  margin-top: 1rem;
  background-color: $primary_light_blue;
  background: linear-gradient(0deg, #2fdaff 0, #538dff 45%, #538dff 65%, #2fdaff 100%);
  // background: linear-gradient(0deg, #fdacff 0, #ff64d4 45%, #ff64d4 65%, #fdacff 100%);
  color: $primary_white;
  transition: 0.3s all ease;
  background-position: 100% 0;
  background-size: 100% 140%;
  border-radius: 2rem;
  cursor: pointer;
  &:hover {
    background-position: 100% 100%;
  }
}
.Support {
  width: 560px;
  display: flex;
  flex-direction: row;
  background-color: rgba($secondary_black, 0.4);
}
.SupportBtn {
  cursor: pointer;
  font-size: 1rem;
  opacity: 0.7;
}
.SupportBtnHelp {
  cursor: pointer;
  font-size: 1rem;
  opacity: 0.7;
  a {
    color: white;
    text-decoration: none;
  }
}
.PreviousPool {
  opacity: 0.7;
}
.BackTo {
  background-color: transparent;
  border-radius: 0.5rem;
  border-style: solid;
  opacity: 0.3;
  color: white;
  font-size: 1rem;
  padding: 0.3rem 0.7rem;
}
